.hero {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #f0f2f5;
}

.hero .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: left;
  gap: 4rem;
}

.hero h1 {
  font-size: 3.5rem;
  font-weight: 700;
  color: var(--primary-color);
  margin-bottom: 0;
}

.hero h2 {
  font-size: 2rem;
  font-weight: 600;
  color: var(--heading-color);
  margin-bottom: 1rem;
}

.hero .intro {
  font-size: 1.2rem;
  color: var(--secondary-color);
}

.hero .bio {
  font-size: 1.1rem;
  max-width: 600px;
  margin-bottom: 2rem;
}

.hero-buttons .btn {
  margin-right: 15px;
}

.profile-image {
  width: 350px;
  height: 350px;
  border-radius: 50%;
  object-fit: cover;
  border: 5px solid var(--primary-color);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

/* Responsive adjustments */
@media (max-width: 992px) {
  .hero .container {
    flex-direction: column;
    text-align: center;
    gap: 2rem;
  }

  .text-content {
    order: 2; /* Puts the text below the image on small screens */
  }

  .image-content {
    order: 1; /* Puts the image at the top on small screens */
  }
}

@media (max-width: 576px) {
  .hero h1 {
    font-size: 2.5rem;
  }
  .hero h2 {
    font-size: 1.5rem;
  }
  .profile-image {
    width: 250px;
    height: 250px;
  }
}